@import 'variables.scss';

* {
    margin : 0;
    padding : 0;
    box-sizing : border-box;
}

a {
    text-decoration : none;
}


#content {
    min-height: 100vh;
    width: 100%;
    height: auto;
    padding : 2rem;

    * {
        margin : .5rem 0rem;
    }

    .waifu-title-wrapper {
        display : flex;
        justify-content: space-between;
        align-items: center;

        .waifu-name-title,
        .waifu-content-title {
            position: relative;
            flex : 0 0 45%;
            font-size : 2.5rem;
            color : #2b2b2b;
            text-align : center;
            padding : .5rem  1.5rem;
        }

        .waifu-name-title::after {
            content: " ";
            position : absolute;
            bottom : 0;
            left : calc((100% / 2) - (60% /  2) );
            height: 3px;
            width: 60%;
            margin : auto;
            background-color : $base-color-pink;
            // z-index: 10;
        }

    }

    .waifu-detail-wrapper {
        display: flex;
        margin: 0;
    }



    p {
        color : teal;
    }

    .img-wrapper {
        flex : 1 0 45%;
        width: 100%;
        margin : 1rem 0rem;
        
        .waifu-img {
            display: block;
            margin : auto;
            max-height: 20rem;
        }

        .waifu-price {
            color : #303030;
            margin : 1.5rem;
            text-align : center;
            font-size : 1.7rem;
            letter-spacing: 2px;
        }


        .icon-wrapper {
            display : flex;
            justify-content: space-around;
            margin : auto;
            width: 40%;
            padding : 1rem;
            border-radius : 5px;
            background-color : $base-color-pink;
            backdrop-filter : blur(5px);

            ul {
                display : inline-block;
                list-style-type: none;
                margin : 0;
            }

            .icon-sosmed {
                cursor : pointer;
                display : inline-block;
                padding : 0rem 1rem;
                margin : 0;
                font-size : 1.7rem;
                color : white;
            }
        }

    
    }


    .rating {
        width: 100%;
        display: flex;
        justify-content: center;


        i {
            margin : 0rem .5rem;
            color : $rating-color;
            font-size : 1.5rem;
            text-shadow : 1px 1px 5px rgba(27, 27, 27, 0.473);
        }
    }



    .info-wrapper {
        flex : 1 0 55%;
        display: flex;
        margin : 1rem 0rem;
        flex-direction: column;
        text-align : left;
        padding : 1rem 1.5rem;
        font-size: 1.1rem;
        color : #444444;
        background-color: #f3f3f3;
        box-shadow : 1px 2px 10px #24242488;
        border-radius : 5px;

        .info-title {
            text-align : left;
            font-size : 1.5rem;
            
            .waifu-name {
                margin-left : .5rem;
                color : #2e2e2e;
                font-weight : 600;
            }
        }

        .info-item {
            margin : .5rem;
            color : #2e2e2e;
            font-weight: 400;

            .info-value {
                margin-left : .5rem;
                font-weight: 600;
                color : #2e2e2e;
            }

            .fa-star {
                color : $rating-color;
            }
        }

        .info-desc {
            margin : .5rem;
            color : #2e2e2e;

            .desc-title {
                color : inherit;
                font-weight: 600;
            }

            .desc-content {
                line-height : 1.8;
                text-indent: 20px;
                color : inherit;
            }
        }

        .waifu-price {
            color : #2e2e2e;
            margin : 1.5rem;
            text-align : left;
            font-size : 1.5rem;
            letter-spacing: 2px;
        }

        .btn-rent {
            width: 40%;
            position : relative;
            padding : .4rem .9rem;
            font-size : 1.2rem;
            font-weight : 600;
            border-radius : 5px;
            color : #eeeeee;
            overflow: hidden;
            background-color : $base-color-pink;
            a {
                color : #ececec;
                z-index : 2;
            }
        }

        .btn-rent::before {
            content : " ";
            position : absolute;
            top : 0;
            left : -40%;
            z-index: 1;
            width : 30%;
            height : 100%;
            transform: skew(45deg);
            transition : left 1s ease-out;
            background-color : #e0e0e0;
            backdrop-filter: blur(5px);
        }

        .btn-rent:hover::before {
            left : 400px;
        }
    }


}

